<template>
    <table
        :emptyPage="emptyPage"
        :numsGoods="numsGoods"
        v-if="!emptyPage"
        class="comparison-table"
    >
        <tbody>
            <template
                :data="data"
                v-for="parameter, key, index in data[0]"
                :key="parameter"
            >
                <tr
                    v-if="index != 0"
                    class="comparison-table__row"
                >
                    <td
                        :displayFirstColumn="displayFirstColumn"
                        v-if="displayFirstColumn"
                        class="comparison-table__name"
                    >{{ parameter.Name }}</td>

                    <td
                        v-for="value in data"
                        :key="value.ID"
                        class="comparison-table__cell"
                    >
                        <img
                            v-if="index === 1"
                            :src="getTableValue(value, parameter)"
                            alt="Изображение товара"
                        />

                        <p
                            v-if="index === 2"
                            class="comparison-table__title"
                        >{{ getTableValue(value, parameter) }}</p>

                        <div
                            v-if="index === 3"
                            class="comparison-table__price-group"
                        >
                            <action-button
                                class="comparison-table__buy-button action-button_type_iconed action-button_icon_buy">
                                Добавить в корзину
                            </action-button>

                            <div class="comparison-table__price">
                                <span class="comparison-table__price-item">
                                    {{ getTableValue(value, parameter).NewPrice }}
                                </span>
                                <span class="comparison-table__price-item comparison-table__price-item_disabled">
                                    {{ getTableValue(value, parameter).OldPrice }}
                                </span>
                            </div>
                        </div>

                        <div
                            v-if="index > 3"
                            class="comparison-table__good-information"
                        >
                            {{ getTableValue(value, parameter) }}
                        </div>

                        <action-button
                            v-if="index === 1"
                            @click="deleteHandler(value.ID)"
                            class="comparison-table__remove-goods action-button_type_iconed action-button_icon_remove"
                        >Удалить</action-button>
                    </td>

                    <td v-if="data.length < numsGoods" class="comparison-table__cell">
                        <p
                            v-if="index === 1"
                            class="comparison-table__message"
                        >Добавьте товары для сравнения <Link class="no-wrap" href="#">из каталога</Link></p>
                    </td>
                </tr>
            </template>
        </tbody>
    </table>
</template>

<script>
  import ActionButton from '../ActionButton/ActionButton.vue';
  import Link from '../Link/Link.vue';

  let globalTableKey = undefined;

  export default {
    name: 'comparison-table',
    props: ['data', 'emptyPage', 'displayFirstColumn', 'numsGoods'],
    components: {
      ActionButton,
      Link,
    },
    data() {
      return {};
    },
    methods: {
      getKeyByValue(object, value) {
        return Object.keys(object).find((key) => object[key] === value);
      },

      getTableValue(obj, parameter) {
        const key = this.getKeyByValue(obj, parameter);
        const val = obj[key];
        if (val) {
          globalTableKey = key;
          return val.Value;
        }
        return obj[globalTableKey].Value;
      },

      deleteHandler(id) {
        this.$emit('deleteHandler', id);
      },
    },
  };
</script>
