<template>
    <div class="comparison-section">
        <action-button
            v-if="!emptyPage"
            @click="clearComparison"
            class="comparison-section__clear action-button_icon_delete"
        >Очистить сравнение</action-button>

        <div
            v-if="!emptyPage"
            class="comparison-section__navigation"
        >
            <navigation-button
                @click="previousNavigationHandler"
                class="navigation-button_type_previous"
                :disabled="disablePreviousButton"
            >Предыдущие</navigation-button>

            <navigation-button
                @click="nextNavigationHandler"
                class="navigation-button_type_next"
                :disabled="disableNextButton"
            >Предыдущие</navigation-button>
        </div>

        <div v-if="!emptyPage" class="comparison-filter">
            <ul class="radio-list">
                <li class="radio-list__item">
                    <radio-field id="comparison-parametr-all" checked="true">Все параметры</radio-field>
                </li>

                <li class="radio-list__item">
                    <radio-field id="comparison-parametr-different">Только отличающиеся</radio-field>
                </li>
            </ul>
        </div>

        <div class="comparison-data">
            <comparison-table
                :data="goodsDisplay"
                :emptyPage="emptyPage"
                :numsGoods="numsGoods"
                :displayFirstColumn="displayFirstColumn"
                @deleteHandler="deleteHandler"
            />

            <p v-if="emptyPage" class="comparison-data__message">Добавьте товары для сравнения <Link class="no-wrap">из каталога</Link></p>

        </div>
    </div>
</template>

<script>
import ActionButton from '../ActionButton/ActionButton';
import NavigationButton from '../NavigationButton/NavigationButton';
import RadioField from '../RadioField/RadioField';
import Link from '../Link/Link';
import ComparisonTable from '../ComparisonTable/ComparisonTable';

export default {
  name: 'comparison-section',

  components: {
    ActionButton,
    NavigationButton,
    RadioField,
    Link,
    ComparisonTable,
  },

  data() {
    return {
      emptyPage: false,
      goodsDisplay: [],
      numsGoods: null,
      initialGoodsIndex: 0,
      tableKey: null,
      disablePreviousButton: true,
      disableNextButton: true,
      displayFirstColumn: true,
      goods: [
        {
          ID: { Name: '', Value: '0' }, Image: { Name: '', Value: require('../../assets/goods/01.jpg') }, Title: { Name: '', Value: 'Штоф «Моряк ВМФ»' }, Price: { Name: '', Value: { NewPrice: '3 600 руб.', OldPrice: '' } }, Article: { Name: 'Артикул', Value: 'B18' }, Size: { Name: 'Размер', Value: '34см*36см*7см' }, ArticleAddition: { Name: 'Артикул', Value: '170х245 мм 34см*36см*7см' }, Material: { Name: 'Материал', Value: 'Материал трубы: латунь, красное дерево Материал шкатулки: красное дерево' },
        },
        {
          ID: { Name: '', Value: '2' }, Image: { Name: '', Value: require('../../assets/goods/02.jpg') }, Title: { Name: '', Value: 'Садовый фонтан «Щенки»' }, Price: { Name: '', Value: { NewPrice: '11 200 руб.', OldPrice: '' } }, Article: { Name: 'Артикул', Value: 'BG5510M' }, Size: { Name: 'Размер', Value: '170х245 мм' }, ArticleAddition: { Name: 'Артикул', Value: '–' }, Material: { Name: 'Материал', Value: '–' },
        },
        {
          ID: { Name: '', Value: '3' }, Image: { Name: '', Value: require('../../assets/goods/03.jpg') }, Title: { Name: '', Value: 'Книга в кожаном переплете «Иудейские праздники» BG1394F' }, Price: { Name: '', Value: { NewPrice: '21 800 руб.', OldPrice: '26 800 руб' } }, Article: { Name: 'Артикул', Value: 'BG1394F' }, Size: { Name: 'Размер', Value: '205х265 мм' }, ArticleAddition: { Name: 'Артикул', Value: '–' }, Material: { Name: 'Материал', Value: '–' },
        },
        {
          ID: { Name: '', Value: '4' }, Image: { Name: '', Value: require('../../assets/goods/03.jpg') }, Title: { Name: '', Value: 'Книга в кожаном переплете «Иудейские праздники» BG1394F' }, Price: { Name: '', Value: { NewPrice: '21 800 руб.', OldPrice: '' } }, Article: { Name: 'Артикул', Value: 'BG1394F' }, Size: { Name: 'Размер', Value: '205х265 мм' }, ArticleAddition: { Name: 'Артикул', Value: '–' }, Material: { Name: 'Материал', Value: '–' },
        },
        {
          ID: { Name: '', Value: '5' }, Image: { Name: '', Value: require('../../assets/goods/01.jpg') }, Title: { Name: '', Value: 'Штоф «Моряк ВМФ»' }, Price: { Name: '', Value: { NewPrice: '3 600 руб.', OldPrice: '' } }, Article: { Name: 'Артикул', Value: 'B18' }, Size: { Name: 'Размер', Value: '34см*36см*7см' }, ArticleAddition: { Name: 'Артикул', Value: '170х245 мм 34см*36см*7см' }, Material: { Name: 'Материал', Value: 'Материал трубы: латунь, красное дерево Материал шкатулки: красное дерево' },
        },
      ],
    };
  },

  created() {
    this.disableNextButton = !(this.goods.length > this.goodsDisplay.length);

    if (window.innerWidth < 700) {
      this.displayFirstColumn = false;
    } else {
      this.displayFirstColumn = true;
    }

    if (window.innerWidth < 1000) {
      this.numsGoods = 2;
      this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + this.numsGoods);
    } else {
      this.numsGoods = 3;
      this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + this.numsGoods);
    }

    window.addEventListener('resize', this.resizeEventHandler);
  },

  unmounted() {
    window.removeEventListener('resize', this.resizeEventHandler);
  },

  methods: {
    resizeEventHandler() {
      if (window.innerWidth < 700) {
        this.displayFirstColumn = false;
      } else {
        this.displayFirstColumn = true;
      }

      if (window.innerWidth < 1000) {
        this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + 2);
        this.numsGoods = 2;
      } else {
        this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + 3);
        this.numsGoods = 3;
      }
    },

    clearComparison() {
      this.emptyPage = true;
    },

    deleteHandler(id) {
      for (let index = 0; index < this.goodsDisplay.length; index++) {
        if (this.goodsDisplay[index].ID === id) {
          for (let inx = 0; inx < this.goods.length; inx++) {
            if (this.goods[inx].ID === id) {
              this.goods.splice(inx, 1);
            }
          }

          this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + this.numsGoods);

          if (this.goods.length === 0) {
            this.emptyPage = true;
          }

          if (this.goods.length === this.goodsDisplay.length) {
            this.disableNextButton = true;
          }
        }
      }
    },

    nextNavigationHandler() {
      if (this.initialGoodsIndex + this.numsGoods < this.goods.length) {
        this.initialGoodsIndex += this.numsGoods;
        this.disablePreviousButton = false;

        if (this.initialGoodsIndex + this.numsGoods > this.goods.length) {
          this.disableNextButton = true;
        }
      }

      this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + this.numsGoods);
    },

    previousNavigationHandler() {
      if (this.initialGoodsIndex - this.numsGoods >= 0) {
        this.initialGoodsIndex -= this.numsGoods;
        this.disableNextButton = false;

        if (this.initialGoodsIndex - this.numsGoods < 0) {
          this.disablePreviousButton = true;
        }
      }

      this.goodsDisplay = this.goods.slice(this.initialGoodsIndex, this.initialGoodsIndex + this.numsGoods);
    },
  },
};
</script>

<style lang="scss">
    @import "../../components/RadioList/RadioList.scss";
    @import "./ComparisonSection.scss";
    @import "../../components/ComparisonData/ComparisonData.scss";
    @import "../../components/ComparisonTable/ComparisonTable.scss";
</style>
